@import url(https://fonts.googleapis.com/css2?family=Pacifico&family=Roboto:wght@300;900&display=swap);
body {
  box-sizing: border-box;
  margin: 0;
  font-family: 'Roboto', sans-serif;
  background: #fafafa;
  color: rgba(0, 0, 0, 0.67);
}

code {
  font-family: 'Roboto', sans-serif;
}

.welcomeScreen {
  box-sizing: border-box;
  height: 100%;
  padding: 12px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.welcomeScreen h1 {
  margin: 0;
  font-size: 48px;
  font-family: 'Pacifico', cursive;
  text-align: center;
}

.welcomeScreen button {
  width: 96px;
  height: 96px;
  border-radius: 50%;
  outline: none;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
  margin: 0 24px;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  font-weight: 900;
  color: rgba(0, 0, 0, 0.67);
}

.welcomeScreen button:hover {
  box-shadow: 0rem 0rem 0.6rem 0.2rem rgba(0, 0, 0, 0.26);
}

.welcomeScreen button:active {
  box-shadow: 0rem 0rem 1.2rem 0.2rem rgba(0, 0, 0, 0.26);
  border: none;
}

.welcomeScreen button:focus {
  border: none;
  outline: none;
}

.btnContainer button {
  width: 48px;
  height: 48px;
  outline: none;
  background: #fafafa;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  padding: 0;
  margin: 0 24px;
}

.btnContainer button:hover {
  box-shadow: 0rem 0rem 0.6rem 0.2rem rgba(0, 0, 0, 0.26);
}

.btnContainer button:active {
  box-shadow: 0rem 0rem 1.2rem 0.2rem rgba(0, 0, 0, 0.26);
  border: none;
}

.btnContainer button:focus {
  border: none;
  outline: none;
}

.btnContainer {
  width: 100%;
  height: 96px;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (max-width: 415px) {
  .btnContainer {
    position: fixed;
    height: 64px;
    z-index: 100;
    left: 0;
    bottom: 0;
  }
}

.card {
  box-sizing: border-box;
  height: 700px;
  display: grid;
  grid-template-rows: 1fr 188px;
}

.card__img {
  background: chocolate;
  overflow: hidden;
}

.card__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.card__header {
  box-sizing: border-box;
  padding: 12px;
  height: 64px;
  border-bottom: 1px solid #c8c8c8;
}

.card__header h1 {
  margin: 0;
  font-weight: 900;
  font-size: 24px;
}

.card__header h3 {
  margin: 0;
  font-weight: 300;
  font-size: 12px;
}

.card__description {
  box-sizing: border-box;
  padding: 12px;
  height: 124px;
}

.card__description p {
  font-weight: 300;
  font-size: 12px;
}

@media (max-width: 415px) {
  .card {
    height: 100vh;
  }

  .card__info {
    position: fixed;
    z-index: 100;
    left: 0;
    bottom: 64px;
  }

  .card__header {
    background: linear-gradient(180deg, rgba(250, 250, 250, 0.7) 0%, #fafafa 61.98%);
    height: 64px;
    padding: 6px 12px;
  }

  .card__header h1 {
    font-size: 20px;
  }

  .card__description {
    background: #fafafa;
    height: 96px;
    padding: 6px 12px;
  }
}

.match {
  box-sizing: border-box;
  text-align: center;
  height: 100%;
  padding: 124px 0;
}

.match h1 {
  margin: 0;
  font-size: 48px;
  font-family: 'Pacifico', cursive;
  color: #3cdb9a;
}

.match img {
  width: 124px;
  height: 124px;
  border-radius: 50%;
  object-fit: cover;
  border: 6px solid #3cdb9a;
}

.noMoreMovies {
  box-sizing: border-box;
  text-align: center;
  height: 100%;
  padding: 124px 12px;
}

.noMoreMovies h1 {
  margin: 0;
  font-size: 48px;
  font-family: 'Pacifico', cursive;
  color: #ff6b6b;
}

.card__container {
  height: 796px;
}

.container-shadow {
  box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.36);
}

.btn-again {
  background: none;
  border: none;
  width: 248px;
  height: 48px;
  margin-top: 48px;
  font-family: 'Roboto', sans-serif;
  font-size: 24px;
  cursor: pointer;
}

.btn-again:hover {
  box-shadow: 0rem 0rem 0.6rem 0.2rem rgba(0, 0, 0, 0.26);
}

.btn-again:active {
  box-shadow: 0rem 0rem 1.2rem 0.2rem rgba(0, 0, 0, 0.26);
  border: none;
}

.btn-again:focus {
  border: none;
  outline: none;
}

.error__message {
  width: 100%;
  text-align: center;
  margin-top: 96px;
}

@media (max-width: 415px) {
  .card__container {
    height: 100vh;
  }
  .container-shadow {
    box-shadow: none;
  }
}

.App {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.App__content {
  max-width: 360px;
  max-height: 796px;
  width: 100%;
  height: 100%;
}

